import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Observable, of } from 'rxjs';
import { IConfigDefinition, ImpactRowVisibility, ShowInLocationOptions } from './config-definition.types';

@Injectable({
  providedIn: 'root'
})
export class RxConfigDefinitionService {
  private url = '/api/rx/application/admin-settings/component';

  constructor(private httpClient: HttpClient, private translateService: TranslateService) {}

  getComponents(): Observable<string[]> {
    return this.httpClient.get<string[]>('/api/rx/application/admin-settings/components-list');
  }

  get(componentName: string): Observable<IConfigDefinition> {
    return this.httpClient.get<IConfigDefinition>(`${this.url}/${encodeURIComponent(componentName)}`);
  }

  update(componentName: string, configDefinition: IConfigDefinition): Observable<any> {
    return this.httpClient.put(`${this.url}/${encodeURIComponent(componentName)}`, configDefinition);
  }

  create(configDefinition: IConfigDefinition): Observable<any> {
    return this.httpClient.post(`${this.url}`, configDefinition);
  }

  getNew(isApplication: boolean): Observable<IConfigDefinition> {
    return of({
      viewToOpen: 'CommonSettings',
      componentName: null,
      externalLink: null,
      impactRowVisibility: ImpactRowVisibility.None,
      localeList: [
        {
          componentLabel: this.translateService.instant(
            'com.bmc.arsys.rx.client.config-designer.default-component-label.label'
          ),
          firstMenu: null,
          locale: 'en',
          secondMenu: null
        }
      ],
      parentComponentName: null,
      permissions: [],
      registeredModuleName: null,
      settingMetaData: [],
      showInLocation: isApplication ? ShowInLocationOptions.Application : ShowInLocationOptions.InnovationStudio,
      supportsMultiple: false,
      viewComponent: false
    });
  }
}
